body {
  font-size: 16px;
}

.tipser-element a {
  color: #fff;
}

.menu {
  list-style: none;
  padding-left: 0;
}

.menu-item {
  padding-left: 35px;
  text-transform: uppercase;
  color: white;
}

.link {
  color: blue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  height: 300px;
  background: #eee;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #e0e0e0;
}

.footer-text {
  color: #bbbbbb;
  font-size: calc(10px + 2vmin);
}
